







































































import {Component, Vue} from 'vue-property-decorator';
import {Category, Request} from "@/api/URI";
import {Template, TemplateState} from "@/template/Template";
import {OMS} from "@/OMS";
import {Auth} from "@/api/auth/Auth";
import {User} from "@/api/auth/User";
import {TemplateStorage} from "@/api/TemplateStorage";
import {BrowserUtils} from "@/BrowserUtils";
import {CRUD} from "@/api/CRUD";
import {TemplateHead} from "@/template/TemplateHead";

@Component({
  name: "ProjectSelectionView",
  components: {},
})
export default class ProjectSelectionView extends Vue {

  get auth(): User | null {
    return Auth.getUser();
  }

  projects: Array<Template> = [];
  isCrateModalActive = false;
  isLoading = true;

  async mounted() {
    OMS.setCustomStyleSource(``);
    // @ts-ignore
    OMS.setSelectedElement({});
    BrowserUtils.setPageTitle("ออกแบบแม่แบบ - SAMT Template Builder");

    const crud = new CRUD(Category.TEMPLATE);
    crud.setRequest(Request.GET);
    await crud.shoot({});
    this.projects = crud.getResult();
    this.isLoading = crud.status;
  }

  onClick(project: Template) {
    TemplateStorage.setTemplate(project.id).then(() => {
      OMS.getInstance().$router.push({name: "Builder@index"}).then(() => {
        OMS.setCustomStyleSource(`/assets/oms/dynamic/customstyle.php/?id=${project.id}`);
      });
    });
    this.$store.state.template = OMS.parseTemplate(TemplateStorage.getTemplate());
  }

  async createNewTemplate() {
    const crud = new CRUD(Category.TEMPLATE);
    crud.setRequest(Request.CREATE);
    crud.setAttachData(new Template(
        0,
        `แม่แบบใหม่ของ ${Auth.getUser()?.username}`,
        "คำอธิบายแม่แบบ",
        new TemplateHead("หัวข้อเว็บไซต์", "", [], []),
        [],
        "",
        Auth.getUser() as User,
        TemplateState.DRAFT,
        ['TITLE', 'SUBTITLE'],
        0))
    await crud.shoot({});

    if(crud.getResponseInfo().apistatus === "OK") {
      this.onClick(crud.getResult());
      await this.$router.push({name: 'Builder@index'})
    }
  }

}
